
.popup {
  position: absolute;
  background: #ffffff;
  box-shadow: 0 0 3px 1px rgba(81, 82, 84, 0.23);
  z-index: 999;
  border-radius: 5px;
  outline: none;
}
.popup .popup_pin {
    margin: 13px;
    float: right;
    background: url("../assets/16x16-sprite.png") no-repeat -841px 0;
    width: 16px;
    height: 16px;
}
.popup .popup_pin.pinned {
      width: 100%;
      background: #ebebeb;
      margin: 0px;
      border-radius: 5px 5px 0px 0px;
      height: 18px;
      position: absolute;
      cursor: move;
}
.popup .popup_arrow {
    position: absolute;
}
.popup .popup_arrow.top {
      top: -8px;
}
.popup .popup_arrow.top .popup_arrow_border {
        height: 0;
        width: 0;
        overflow: hidden;
        position: absolute;
        top: -2px;
        left: 0;
        border-style: solid;
        border-width: 0 10px 10px 10px;
        border-color: transparent transparent rgba(81, 82, 84, 0.25) transparent;
}
.popup .popup_arrow.top .popup_arrow_body {
        height: 0;
        width: 0;
        overflow: hidden;
        position: absolute;
        top: 0;
        left: 0;
        border-style: solid;
        border-width: 0 10px 10px 10px;
        border-color: transparent transparent #ffffff transparent;
}



