
.json_pollock_v {
  padding: 10px;
  text-align: center;
  background: #162036;
  color: #fff;
  height: 15px;
  line-height: 15px;
}
.go_to_code_btn {
  position: absolute;
  top: 5px;
  right: 8px;
}
.go_to_code_btn .v-switch-core {
    z-index: 1;
}
.pollock_container {
  height: calc( 100% - 35px);
  width: 100%;
  overflow: auto;
}
.pollock_container .dom_parent {
    position: relative;
}
.pollock_container .dom_parent .dom_container {
      position: absolute;
      top: 50%;
      left: 50%;
      min-width: 200px;
      transform: translate(-50%, 45px);
      padding-bottom: 20px;
}
.pollock_container .dom_parent .dom_container.locate_element .lp-json-pollock {
        border: solid;
        border-color: #5879da;
        padding: 2px;
}
.pollock_container .dom_parent .dom_container.locate_element .lp-json-pollock .playground_el {
          position: relative;
}
.pollock_container .dom_parent .dom_container.locate_element .lp-json-pollock .playground_el:hover:after {
            content: ' ';
            border: solid;
            border-color: #200ce8;
            border-width: 2px;
            position: absolute;
            top: -2px;
            left: 0px;
            width: 98%;
            height: 98%;
            cursor: pointer;
            background-color: #3883fa;
            opacity: 0.2;
}



