
.json-template-list {
  color: #2c3e50;
  width: 400px;
  margin: 20px;
  max-height: 300px;
  overflow-x: auto;
}
.json-template-list .json-template-item {
    margin: 3px 0px;
    border-bottom: solid 0.5px;
    padding-top: 3px;
    position: relative;
}
.json-template-list .json-template-item .json-template-item-name {
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      max-width: 300px;
      display: inline-block;
}
.json-template-list .json-template-item .json-template-item-desc {
      font-size: 13px;
      font-weight: normal;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      max-width: 400px;
      display: inline-block;
}
.json-template-list .json-template-item .json-template-item-channels {
      position: absolute;
      right: 0px;
      top: 5px;
}
.json-template-list .json-template-item .json-template-item-channels .json-template-item-channel {
        margin-left: 4px;
}
.json-template-list .json-template-item .json-template-item-channels .json-template-item-channel.web {
          float: right;
          width: 16px;
          height: 16px;
          background: url("../assets/16x16-sprite.png") no-repeat -1441px -1px;
}
.json-template-list .json-template-item .json-template-item-channels .json-template-item-channel.fb {
          float: right;
          width: 16px;
          height: 16px;
          background: url("../assets/16x16-sprite.png") no-repeat -2700px 0;
}
.json-template-list .json-template-item .json-template-item-channels .json-template-item-channel.line {
          float: right;
          width: 16px;
          height: 16px;
          background: url("../assets/16x16-sprite.png") no-repeat -2880px 0;
}
.json-template-list .json-template-item .json-template-item-channels .json-template-item-channel.abc {
          float: right;
          width: 16px;
          height: 16px;
          background: url("../assets/16x16-sprite.png") no-repeat -2280px 0;
}
.json-template-list .json-template-item .json-template-item-channels .json-template-item-channel.rcs {
          float: right;
          width: 16px;
          height: 16px;
          background: url("../assets/16x16-sprite.png") no-repeat -2900px 0;
}



