
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  height: 100%;
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  background-color: rgba(22, 32, 54, 0.8);
}
#app::after {
  content: '';
  background: url(assets/back-blue.jpg);
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}
.splitter-pane-resizer {
  opacity: 1 !important;
  margin-left: -3px !important;
  border: solid #ebebeb !important;
  background: #EBEBED !important;
  width: 8px !important;
  margin-top: 36px;
  height: calc(100% - 98px) !important;
}
.header_container {
  height: 60px;
}
.editor_container, .jsoneditor_container, .jsonpollock_container {
  background-color: #FFFFFF;
}
.jsoneditor_container {
  height: calc(100% - 61px);
}
.jsonpollock_container {
  height: calc(100% - 63px);
  border: solid #162036 1px;
}
.jsonpollock_container.actions-bar-open {
    height: calc(100% - 209px);
    border-bottom: 0px;
}
.actions_container {
  border: solid #ebebee 1px;
  height: 144px;
  overflow: auto;
}
.actions_btn, .actions_toggle, .actions_clear {
  position: absolute;
  height: 22px;
  padding: 0 7px;
  cursor: pointer;
  text-align: center;
  overflow: auto;
  border-radius: 3px;
  background: #ebebeb;
}
.actions_toggle {
  right: 5px;
}
.actions_toggle.open {
    bottom: 208px;
}
.actions_toggle.close {
    bottom: 62px;
}
.actions_clear {
  right: 23px;
  bottom: 208px;
  font-size: 13px;
}



