
.channels-validation {
  color: #2c3e50;
  width: 400px;
  margin: 20px;
}
.channels-validation .channels-validation-title {
    margin-bottom: 10px;
}
.channels-validation .channel-validate-warpper {
    overflow: auto;
    max-height: 700px;
}
.channels-validation .channel-validate-warpper .channel-validate {
      line-height: 30px;
      height: 30px;
      position: relative;
}
.channels-validation .channel-validate-warpper .channel-validate .channel-icon {
        position: absolute;
        left: 56px;
        top: 6px;
        width: 16px;
        height: 16px;
}
.channels-validation .channel-validate-warpper .channel-validate .channel-icon.abc {
          background: url("../assets/16x16-sprite.png") no-repeat -2280px 0;
}
.channels-validation .channel-validate-warpper .channel-validate .channel-icon.fb {
          background: url("../assets/16x16-sprite.png") no-repeat -2700px 0;
}
.channels-validation .channel-validate-warpper .channel-validate .channel-icon.line {
          background: url("../assets/16x16-sprite.png") no-repeat -2880px 0;
}
.channels-validation .channel-validate-warpper .channel-validate .channel-icon.rcs {
          background: url("../assets/16x16-sprite.png") no-repeat -2900px 0;
}
.channels-validation .channel-validate-warpper .channel-validate .channel-icon.web {
          background: url("../assets/16x16-sprite.png") no-repeat -1441px -1px;
}
.channels-validation .channel-validate-warpper .channel-validate .channel-name {
        margin-left: 41px;
}
.channels-validation .channel-validate-warpper .channel-validate .status-icon {
        position: absolute;
        left: 297px;
}
.channels-validation .channel-validate-warpper .channel-validate .status-icon.validating {
          top: 15px;
          animation: spin 1.5s linear infinite;
          transform-origin: top;
}
.channels-validation .channel-validate-warpper .channel-validate .status-icon.valid {
          width: 18px;
          height: 18px;
          top: 6px;
          background: url("../assets/16x16-sprite.png") no-repeat -259px 1px;
}
.channels-validation .channel-validate-warpper .channel-validate .status-icon.invalid {
          width: 18px;
          height: 18px;
          top: 6px;
          background: url("../assets/16x16-sprite.png") no-repeat -600px 1px;
}
.channels-validation .channel-validate-warpper .channel-validate .channel-show-link-error {
        position: absolute;
        left: 344px;
        text-decoration: underline;
}
.channels-validation .channel-validate-warpper .channel-validate .channel-show-link-error:hover {
          color: #008CBA;
}
.channels-validation .channel-validate-warpper .channel-errors {
      max-height: 200px;
      overflow: auto;
      border-top: solid 0.5px;
      border-bottom: solid 0.5px;
      font-size: 14px;
      margin-bottom: 10px;
}
.channels-validation .channel-validate-warpper .channel-errors .channel-error {
        border-top: solid #a3a3a3 0.5px;
        padding: 4px;
}
.channels-validation .channel-validate-warpper .channel-errors .channel-error .channel-error-type {
          font-weight: bold;
          display: block;
}
.channels-validation .channel-validate-warpper .channel-errors .channel-error .channel-error-path {
          font-style: italic;
          font-weight: normal;
          display: block;
}
.channels-validation .channel-validate-warpper .channel-errors .channel-error .channel-error-msg {
          font-weight: bold;
}
.channels-validation .channel-validate-btns {
    margin-top: 10px;
}



