
@import url(../../node_modules/jsoneditor/dist/jsoneditor.css);
div.jsoneditor {
  border: thin solid #162036;
}
div.jsoneditor div.jsoneditor-menu {
    background-color: #162036;
    border-bottom: 1px solid #162036;
}
div.jsoneditor div.jsoneditor-contextmenu ul li button.jsoneditor-selected {
    background-color: #162036;
}
.jsoneditor-loading {
  position: absolute;
  z-index: 999;
  background: rgba(235, 235, 235, 0.9);
  height: 100%;
  width: 100%;
  text-align: center;
  display: table;
  color: #162036;
}
.jsoneditor-loading span {
    font-size: 30px;
    font-weight: bold;
    display: table-cell;
    vertical-align: middle;
}



