
.my-gists-list {
  color: #2c3e50;
  width: 200px;
  margin: 20px;
}
.my-gists-list .my-gists-title {
    margin: 3px px;
    padding-bottom: 6px;
    border-bottom: solid 0.5px;
}
.my-gists-list .my-gists-wrapper {
    max-height: 300px;
    overflow-x: auto;
}
.my-gists-list .my-gists-item {
    margin: 3px 0px;
    border-bottom: solid 0.5px;
    padding-top: 3px;
    position: relative;
}
.my-gists-list .my-gists-item .my-gists-item-name {
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      max-width: 300px;
      font-weight: normal;
      display: inline-block;
}
.my-gists-list .my-gists-item .my-gists-item-desc {
      font-size: 13px;
      font-weight: normal;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      max-width: 400px;
      display: inline-block;
}



